@use "global" as *;

@include mq("md") {
#blog{
  background-image: url(../../../images/top-page/blog/blog.png);
  background-size: 100% rem(500);
  background-repeat: no-repeat;
}}

.blog__inner{
  padding: rem(80) 0;
  max-width: rem(333);
  margin: 0 auto;

@include mq("md") {
  padding: rem(100) 0 rem(137);
  max-width: rem(1080);
  position: relative;
}}

@include mq("md") {
  .blog__section-title{
    color: #fff;
  }}

.blog__title{
  padding-top: rem(1);
  padding-bottom: rem(4);
  font-size: rem(16);
  font-weight: 700;
  line-height: rem(24);
  border-bottom: solid rem(1) $blue;
}

.blog__date{
  padding-top: rem(23);
  font-size: rem(12);
  font-family: "Gotu", sans-serif;
}

.blog__card{
  padding: rem(16);
  margin-top: rem(27);
  margin-bottom: rem(41);
  box-shadow: rem(2) rem(2) rem(10) 0 rgba(0, 0, 0, .25);

  @include mq("md") {
    margin-top: 0;
      background-color: #fff;
    }}

.blog__text{
  padding-top: rem(16);
  padding-bottom: rem(5);
}

@include mq("md") {
.blog__cards{
  display: flex;
  gap: 3.8%;
}}

.blog__subtitle{
  padding-top: rem(6);

@include mq("md") {
  color: #fff;
  padding-bottom: rem(83);
}}

@include mq("md") {
.blog__button{
  margin: rem(39) auto 0;
}}

.card_inner{
  padding-right: rem(24);
}

.tag{
  color: $blue;
  border: solid rem(1) $blue;
  display: inline-block;
}

.card__tag{
  padding: rem(4) rem(12);
  font-size: rem(12);

@include mq("md") {
  padding: rem(7) rem(16);
}}

.card__title{
  padding: rem(7) 0 rem(8);
  position: relative;
  font-size: rem(16);
  text-align: left;
}

.card__title:after{
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  color: $blue;
  border: solid rem(1) $blue;
}

.card__word{
  padding: rem(17) rem(16) rem(24);

@include mq("md") {
  padding: rem(24) rem(16) rem(40);
}}

@include mq("md") {
.blog__bg{
  position: absolute;
  top: rem(22);
  left: rem(-80);
  width: rem(437);
  height: rem(201);
}}